/* ===== Promo block ===== */
.promo-block {
    background: darken($color-primary, 5%);
    .body-green & {
        background: darken($color-green, 5%);
    }
    .body-blue & {
        background: darken($color-blue, 5%);
    }
    .body-orange &{
        background: darken($color-orange, 5%);
    }
    .body-pink &{
        background: darken($color-pink, 5%);
    }
    .body-purple &{
        background: darken($color-purple, 5%);
    }
    
    
    a {
        color: rgba(0, 0, 0, 0.6);
        font-weight: bold;
        &:hover {
            color: rgba(0, 0, 0, 0.7);
        }
    }
    .promo-block-inner {
        padding: 45px;
        color: #fff;
        @include border-radius(4px);

    }
    .promo-title {
        font-size: 20px;
        font-weight: 800;
        margin-top:0;  
        margin-bottom: 45px;   
        .svg-inline--fa {
            color: rgba(0, 0, 0, 0.6);
        }   
    }
    .figure-holder-inner {
        background: #fff;
        margin-bottom: 30px;
        position: relative;
        text-align: center;
        img {
            border: 5px solid #fff;
        }
        .mask {
            display: block;
            visibility: hidden;
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: rgba(0, 0, 0, 0.7);
            cursor: pointer;
            text-decoration: none;
            .svg-inline--fa {
                color: #fff;
                font-size: 36px;
                display: inline-block;
                position: absolute;
                top: 50%;
                left: 50%;
                margin-left: -18px;
                margin-top: -18px;
                &.pink {
                    color: $color-pink;
                }
    
            }
        }
        &:hover {
            .mask {
                visibility: visible;
            }
        }

    }
    
    .content-holder-inner {
        padding-left: 15px;
        padding-right: 15px;
    }
    
    .content-title {
        font-size: 16px;
        font-weight: 600;
        margin-top: 0;
    }
    
    .highlight {
        color: rgba(0, 0, 0, 0.6);
    }
    
    .btn-cta {
        background: rgba(0, 0, 0, 0.35);
        border: none;
        color: #fff !important;
        margin-bottom: 15px;
        &:hover {
            background: rgba(0, 0, 0, 0.5);
            border: none;
            color: #fff !important;
        }
    }
}

/* Extra small devices (phones, less than 768px) */
@media (max-width: 767px) { 
     .doc-content {
         //margin-left: 0;
     }
     
     .jumbotron {
         padding: 30px 15px;
         h1 {
             font-size: 24px;
             margin-bottom: 15px;
         }
         p {
             font-size: 18px;
         }
     }
     .promo-block .promo-block-inner {
         padding: 30px 15px;
     }
     
     .promo-block .content-holder-inner {
         padding: 0;
     }
     .promo-block .promo-title {
         margin-bottom: 30px;
     }
     
}

